import React, {Component} from 'react';
import s from './Menu.module.css'
import {Link} from 'react-router-dom'


class Menu extends Component{
	handleClick = (obj,text) => {
		this.props.socket.removeListener('resInformation')
		this.props.socket.emit('getInformation',obj, localStorage.Id);
	}
	render(){
		return(
			<ul className={s.mainUl}>
				<li>
					<Link to='/app/main'>
						<div>
							
						</div>
					</Link>
				</li>
				<li>
					<Link to='/app/storage'>
						<div onClick={this.handleClick.bind(this, 'Storage')}>
							
						</div>
					</Link>
				</li>
				<li>
					<div> </div>
					<ul className={s.subUl}>
						<li><Link to='/app/arrival'><div onClick={this.handleClick.bind(this, 'Arrival')}> </div></Link></li>
						<li><Link to='/app/real'><div onClick={this.handleClick.bind(this, 'Real')}> </div></Link></li>
					</ul>
				</li>
				<li>
					<Link to='/app/statistics'>
						<div>
							
						</div>
					</Link>
				</li>
			</ul>
		)
	}
}

export default Menu;
