import React, {Component} from 'react';
import s from './Menu.module.css'
import {Link} from 'react-router-dom'


class Menu extends Component{
	handleClick = (obj,text) => {
		this.props.socket.emit('getInformation',obj);
	}
	render(){
		return(
			<ul className={s.mainUl}>
				<li>
					<Link to='/app/main'>
						<div>
							
						</div>
					</Link>
				</li>
				<li>
					<div></div>
					<ul className={s.subUl}>
						<li><Link to='/app/tovars'><div onClick={this.handleClick.bind(this, 'Tovars')}></div></Link></li>
						<li><Link to='/app/manufacturers'><div onClick={this.handleClick.bind(this, 'Manufacturers')}></div></Link></li>
						<li><Link to='/app/type'><div onClick={this.handleClick.bind(this, 'Type')}></div></Link></li>
						<li><Link to='/app/providers'><div onClick={this.handleClick.bind(this, 'Providers')}></div></Link></li>
						<li><Link to='/app/people'><div onClick={this.handleClick.bind(this, 'People')}> </div></Link></li>
					</ul>
				</li>
				<li>
					<Link to='/app/storage'>
						<div onClick={this.handleClick.bind(this, 'Storage')}>
							
						</div>
					</Link>
				</li>
				<li>
					<div></div>
					<ul className={s.subUl}>
						<li><Link to='/app/statistics/arrival'><div onClick={this.handleClick.bind(this, 'StatisticsArrival')}> </div></Link></li>
						<li><Link to='/app/statistics/real'><div onClick={this.handleClick.bind(this, 'StatisticsReal')}> </div></Link></li>
						<li><Link to='/app/statistics'><div>  </div></Link></li>
					</ul>
				</li>
			</ul>
		)
	}
}

export default Menu;
